<template>
	<view>
		<!-- 发布收购页面 -->
		<view class="releaseSellsHead">

			<!-- 商品基本信息 -->
			<view class="rBuyBase">
				<view class="rBuyBase-title">商品</view>
				<view class="rBuyBase-img"><img class="rBuyBase-pic" src="" alt="" @click="GetBuyImg"></view>
				<view class="rBuyBase-info">

					<view class="sellsBase-name" @click="GetBuyName">
						{{releaseBuyInfo.name}}
					</view>
					<view class="sellsBase-class" @click="">
						{{releaseBuyInfo.class2}}-{{releaseBuyInfo.class3}}
					</view>
				</view>
			</view>

			<view class="rBuyBase">
				<view class="rBuyBase-title">价格</view>
				<input v-model="releaseBuyInfo.buyPrice" class="rBuyBase-input" type="digit" placeholder="请输入金额"
					@blur="blur" />

			</view>
			<view class="rSellsTips">成交均价xxx.00元</view>

			<view class="rBuyBase">
				<view class="rBuyBase-title">数量</view>
				<input v-model="releaseBuyInfo.buyNum" class="rBuyBase-input" type="number" placeholder="请输入数量" />

			</view>

		</view>

		<view class="rBuyBaseRPrice">
			本次收购需要冻结的金额为 ：<text class="text">￥价格</text>
		</view>
		<!-- 收购规则 -->
		<view class="reRules">
			<!-- 规则标题 -->
			<view class="buyRule">
				<view class="logo">?</view>
				<view>
					出售规则
				</view>
			</view>
			<!-- 规则详情 -->

			<view class="text">
				1.发布收购，发布后系统会根据所有挂单售价由高到低显示，当有卖家选择闪购，自动按照最高价成交。
			</view>
			<view class="text">
				2.发布收购需要冻结您全部收购单金额的余额，余额不足时需充值。
			</view>
			<view class="text">
				3.冻结余额在成功收购时直接付给卖家。
			</view>
			<view class="text">
				4.当收购价高于已有挂单的售价，将会以售价和对方直接成交。
			</view>
			<view class="text">
				5.发取消收购时解除冻结可提现。
			</view>

		</view>

