			blur(e) {
				this.releaseBuyInfo.buyPrice = Math.floor(e.detail.value)
				if (this.releaseBuyInfo.buyPrice <= 0) {
					this.releaseBuyInfo.buyPrice = 0
				}
			},
			// 获取图片
